'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { useRouter, useSearchParams } from 'next/navigation';
import { toast } from 'sonner';
import { useAuth } from '@/hooks/useAuth';
import { format } from 'date-fns';
import { id as localeId } from 'date-fns/locale';
import Link from 'next/link'; // <--- ADDED THIS IMPORT
import { 
    Calendar as CalendarIcon, 
    Clock, 
    User, 
    CreditCard, 
    CheckCircle2, 
    Image as ImageIcon,
    ArrowRight,
    ArrowLeft,
    Loader2
} from 'lucide-react';

// UI Components
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Calendar } from '@/components/ui/calendar';
import {
    Popover,
    PopoverContent,
    PopoverTrigger,
} from "@/components/ui/popover";
import { cn } from '@/lib/utils';

// Types
import { Booking, PaymentMethod } from '@/types';

export default function BookingWizardPage() {
    const router = useRouter();
    const searchParams = useSearchParams();
    const { user } = useAuth({ middleware: 'auth' });

    // --- STATE UTAMA ---
    const [step, setStep] = useState(1);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [bookedDates, setBookedDates] = useState<Date[]>([]); // Tanggal yang sudah ada booking

    // --- STATE DATA FORM ---
    const [formData, setFormData] = useState({
        // Step 1
        nama_lengkap: '',
        email: '',
        nomor_telepon: '',
        nama_club: '', // Field tambahan (misal masuk ke catatan)
        
        // Step 2
        tanggal_booking: undefined as Date | undefined,
        jam_mulai: '',
        durasi_jam: '1',
        
        // Step 3
        payment_method_id: '',
        jumlah_dp: '',
        bukti_pembayaran: null as File | null,
    });

    // --- DATA MASTER ---
    const [paymentMethods, setPaymentMethods] = useState<PaymentMethod[]>([]);
    const [lapanganId, setLapanganId] = useState(1); // Default Lapangan 1
    const [hargaPerJam] = useState(100000); // Asumsi harga (bisa fetch dari API)

    // 1. Fetch Data Awal
    useEffect(() => {
        const initData = async () => {
            if (user) {
                setFormData(prev => ({
                    ...prev,
                    nama_lengkap: user.name,
                    email: user.email,
                    nomor_telepon: user.nomor_telepon || '',
                }));
            }

            // Ambil Payment Methods
            try {
                const resPay = await axios.get('/api/paymentMethods');
                setPaymentMethods(resPay.data.filter((p: any) => p.is_aktif));

                // Ambil Booking untuk menandai kalender
                const resBook = await axios.get('/api/bookings');
                const dates = resBook.data.map((b: Booking) => new Date(b.tanggal_booking));
                setBookedDates(dates);

            } catch (error: any) {
                // --- IMPROVED ERROR LOGGING ---
                console.error("Gagal memuat data:", error);
                if (error.response) {
                    console.error("Response data:", error.response.data);
                    console.error("Response status:", error.response.status);
                }
                toast.error("Gagal memuat data booking.");
            }
        };
        initData();

        // Cek apakah ada tanggal dari URL (dari Homepage)
        const dateParam = searchParams.get('date');
        if (dateParam) {
            setFormData(prev => ({ ...prev, tanggal_booking: new Date(dateParam) }));
            setStep(2); // Langsung lompat ke step 2 jika dari kalender homepage
        }
    }, [user, searchParams]);


    // --- LOGIKA HELPER ---
    
    // Hitung jam selesai otomatis
    const getJamSelesai = () => {
        if (!formData.jam_mulai) return '--:--';
        const [hours, minutes] = formData.jam_mulai.split(':').map(Number);
        const endHours = hours + Number(formData.durasi_jam);
        return `${String(endHours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}`;
    };

    // Hitung total harga
    const totalHarga = Number(formData.durasi_jam) * hargaPerJam;


    // --- NAVIGASI STEP ---

    const nextStep = () => {
        // Validasi Sederhana per Step
        if (step === 1) {
            if (!formData.nama_lengkap || !formData.nomor_telepon) return toast.error("Lengkapi data diri!");
        }
        if (step === 2) {
            if (!formData.tanggal_booking || !formData.jam_mulai) return toast.error("Pilih jadwal main!");
        }
        setStep(prev => prev + 1);
    };

    const prevStep = () => setStep(prev => prev - 1);


    // --- SUBMIT FINAL ---
    const handleSubmit = async () => {
        if (!formData.payment_method_id || !formData.bukti_pembayaran) {
            return toast.error("Pilih pembayaran & upload bukti!");
        }

        setIsSubmitting(true);
        try {
            // 1. Buat Booking (POST /api/bookings)
            const bookingPayload = {
                lapangan_id: lapanganId,
                tanggal_booking: format(formData.tanggal_booking!, 'yyyy-MM-dd'),
                jam_mulai: formData.jam_mulai,
                durasi_jam: formData.durasi_jam,
                payment_method_id: formData.payment_method_id,
                catatan: `Club: ${formData.nama_club}`, // Simpan nama club di catatan
                // acara: 'Latihan' // Opsional
            };

            const resBooking = await axios.post('/api/bookings', bookingPayload);
            const bookingId = resBooking.data.id;

            // 2. Upload Pembayaran (POST /api/bookings/{id}/payment)
            const paymentData = new FormData();
            paymentData.append('jumlah_dp', formData.jumlah_dp || totalHarga.toString()); // Default full payment jika kosong
            paymentData.append('bukti_pembayaran', formData.bukti_pembayaran!);

            await axios.post(`/api/bookings/${bookingId}/payment`, paymentData);

            toast.success("Booking Berhasil!", { description: "Silakan tunggu konfirmasi admin." });
            router.push('/pelanggan/home');

        } catch (error: any) {
            console.error(error);
            const msg = error.response?.data?.message || "Gagal memproses booking.";
            
            if (error.response?.status === 422) {
                // Error Validasi (Bentrok Jadwal)
                toast.error("Jadwal Bentrok!", { description: "Jam yang Anda pilih sudah terisi." });
                setStep(2); // Kembali ke step jadwal
            } else {
                toast.error(msg);
            }
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="min-h-screen bg-white pt-24 pb-12 px-4 md:px-8">
            <div className="max-w-6xl mx-auto">
                
                {/* Header Halaman */}
                <div className="mb-8">
                    <p className="text-gray-500 text-sm">Home / Halaman Booking</p>
                </div>

                <div className="flex flex-col lg:flex-row gap-12">
                    
                    {/* --- SIDEBAR STEPPER (Kiri) --- */}
                    <div className="w-full lg:w-1/4 space-y-8">
                        <div className="space-y-1">
                            <h2 className="text-3xl font-bold text-[#D93F21]">Step {step}</h2>
                            <p className="text-gray-600 text-sm">
                                {step === 1 && "masukan data diri anda"}
                                {step === 2 && "Pilih jadwal pesanan anda"}
                                {step === 3 && "pilih metode pembayaran anda"}
                            </p>
                        </div>

                        <div className="space-y-6 relative">
                            {/* Garis Vertikal Penghubung */}
                            <div className="absolute left-[22px] top-4 bottom-4 w-0.5 bg-gray-200 -z-10"></div>

                            {/* Step 1 Indicator */}
                            <div className="flex items-center gap-4">
                                <div className={`w-12 h-12 rounded-full flex items-center justify-center text-white shadow-md transition-all ${step >= 1 ? 'bg-[#D93F21]' : 'bg-gray-300'}`}>
                                    <User className="w-6 h-6" />
                                </div>
                                <span className={`font-medium ${step === 1 ? 'text-black' : 'text-gray-400'}`}>Data Diri</span>
                            </div>

                            {/* Step 2 Indicator */}
                            <div className="flex items-center gap-4">
                                <div className={`w-12 h-12 rounded-full flex items-center justify-center text-white shadow-md transition-all ${step >= 2 ? 'bg-[#D93F21]' : 'bg-gray-300'}`}>
                                    <CalendarIcon className="w-6 h-6" />
                                </div>
                                <span className={`font-medium ${step === 2 ? 'text-black' : 'text-gray-400'}`}>Pilih Jadwal</span>
                            </div>

                            {/* Step 3 Indicator */}
                            <div className="flex items-center gap-4">
                                <div className={`w-12 h-12 rounded-full flex items-center justify-center text-white shadow-md transition-all ${step >= 3 ? 'bg-[#D93F21]' : 'bg-gray-300'}`}>
                                    <CreditCard className="w-6 h-6" />
                                </div>
                                <span className={`font-medium ${step === 3 ? 'text-black' : 'text-gray-400'}`}>Pembayaran</span>
                            </div>
                        </div>
                    </div>

                    {/* --- FORM CONTENT (Kanan) --- */}
                    <div className="flex-1">
                        <div className="border-l-2 border-[#D93F21] pl-8 md:pl-12 py-2">
                            <h3 className="text-2xl font-bold text-[#D93F21] mb-2">
                                {step === 1 && "Data Diri"}
                                {step === 2 && "Pilih Jadwal"}
                                {step === 3 && "Pembayaran"}
                            </h3>
                            <p className="text-gray-500 text-sm mb-8">
                                {step === 1 && "masukan data diri anda dan lanjutkan ke langkah selanjutnya"}
                                {step === 2 && "pilih jadwal anda anda dan lanjutkan ke langkah selanjutnya"}
                                {step === 3 && "lakukan pembayaran dan masukan bukti pembayaran"}
                            </p>

                            {/* === FORM STEP 1: DATA DIRI === */}
                            {step === 1 && (
                                <div className="space-y-5 max-w-lg animate-in slide-in-from-right-4 duration-300">
                                    <div className="space-y-1">
                                        <Label>Nama Lengkap</Label>
                                        <Input value={formData.nama_lengkap} disabled className="bg-gray-100 border-gray-300 rounded-md" />
                                    </div>
                                    <div className="space-y-1">
                                        <Label>Nomor Telepon</Label>
                                        <Input value={formData.nomor_telepon} disabled className="bg-gray-100 border-gray-300 rounded-md" />
                                    </div>
                                    <div className="space-y-1">
                                        <Label>Email</Label>
                                        <Input value={formData.email} disabled className="bg-gray-100 border-gray-300 rounded-md" />
                                    </div>
                                    <div className="space-y-1">
                                        <Label>Nama Club (Opsional)</Label>
                                        <Input 
                                            placeholder="masukan nama club anda" 
                                            className="border-gray-300 rounded-md bg-gray-50"
                                            value={formData.nama_club}
                                            onChange={e => setFormData({...formData, nama_club: e.target.value})}
                                        />
                                    </div>
                                </div>
                            )}

                            {/* === FORM STEP 2: PILIH JADWAL === */}
                            {step === 2 && (
                                <div className="space-y-6 max-w-lg animate-in slide-in-from-right-4 duration-300">
                                    
                                    {/* Date Picker */}
                                    <div className="space-y-2">
                                        <Label>Tanggal Main</Label>
                                        <Popover>
                                            <PopoverTrigger asChild>
                                                <Button
                                                    variant={"outline"}
                                                    className={cn(
                                                        "w-full justify-start text-left font-normal border-gray-300 bg-gray-50 h-12 rounded-md",
                                                        !formData.tanggal_booking && "text-muted-foreground"
                                                    )}
                                                >
                                                    <CalendarIcon className="mr-2 h-4 w-4" />
                                                    {formData.tanggal_booking ? format(formData.tanggal_booking, "PPP", { locale: localeId }) : <span>Pilih tanggal main</span>}
                                                </Button>
                                            </PopoverTrigger>
                                            <PopoverContent className="w-auto p-0 bg-white rounded-xl shadow-2xl border-none" align="start">
                                                <div className="p-4 bg-white rounded-xl">
                                                    <Calendar
                                                        mode="single"
                                                        selected={formData.tanggal_booking}
                                                        onSelect={(date) => setFormData({...formData, tanggal_booking: date})}
                                                        initialFocus
                                                        disabled={(date) => date < new Date(new Date().setHours(0, 0, 0, 0))}
                                                        modifiers={{
                                                            booked: bookedDates
                                                        }}
                                                        modifiersStyles={{
                                                            booked: { color: '#D93F21', fontWeight: 'bold', textDecoration: 'underline' }
                                                        }}
                                                        className="rounded-md border-none"
                                                        // Styling tambahan agar kalender tidak mepet
                                                        classNames={{
                                                            head_cell: "text-gray-500 rounded-md w-10 font-normal text-[0.8rem] pb-4 pt-2",
                                                            row: "flex w-full mt-2",
                                                            cell: "h-10 w-10 text-center text-sm p-0 relative [&:has([aria-selected])]:bg-accent first:[&:has([aria-selected])]:rounded-l-md last:[&:has([aria-selected])]:rounded-r-md focus-within:relative focus-within:z-20",
                                                            day: "h-10 w-10 p-0 font-normal aria-selected:opacity-100 hover:bg-gray-100 rounded-lg transition-all",
                                                            day_selected: "bg-[#D93F21] text-white hover:bg-[#b9351b] hover:text-white focus:bg-[#D93F21] focus:text-white shadow-md",
                                                            day_today: "bg-gray-100 text-gray-900 font-bold",
                                                            day_outside: "text-gray-300 opacity-50",
                                                            day_disabled: "text-gray-300 opacity-50",
                                                        }}
                                                    />
                                                </div>
                                            </PopoverContent>
                                        </Popover>
                                    </div>

                                    <div className="grid grid-cols-2 gap-4">
                                        <div className="space-y-2">
                                            <Label>Jam Mulai</Label>
                                            <Input 
                                                type="time" 
                                                className="border-gray-300 bg-gray-50 h-12 rounded-md"
                                                value={formData.jam_mulai}
                                                onChange={e => setFormData({...formData, jam_mulai: e.target.value})}
                                            />
                                        </div>
                                        <div className="space-y-2">
                                            <Label>Jam Akhir (Otomatis)</Label>
                                            <div className="flex items-center px-3 border border-gray-300 bg-gray-200 h-12 rounded-md text-gray-600">
                                                {getJamSelesai()}
                                            </div>
                                        </div>
                                    </div>

                                    <div className="space-y-2">
                                        <Label>Total Durasi (Jam)</Label>
                                        <Input 
                                            type="number" 
                                            min="1" 
                                            max="5"
                                            className="border-gray-300 bg-gray-50 h-12 rounded-md"
                                            value={formData.durasi_jam}
                                            onChange={e => setFormData({...formData, durasi_jam: e.target.value})}
                                        />
                                    </div>
                                </div>
                            )}

                            {/* === FORM STEP 3: PEMBAYARAN === */}
                            {step === 3 && (
                                <div className="space-y-6 max-w-lg animate-in slide-in-from-right-4 duration-300">
                                    
                                    {/* Payment Selection */}
                                    <div className="space-y-2">
                                        <Label>Jenis Pembayaran</Label>
                                        <div className="grid grid-cols-2 gap-3">
                                            {paymentMethods.map(method => (
                                                <div 
                                                    key={method.id}
                                                    onClick={() => setFormData({...formData, payment_method_id: method.id.toString()})}
                                                    className={`
                                                        cursor-pointer border rounded-lg p-3 flex flex-col items-center justify-center gap-2 transition-all
                                                        ${formData.payment_method_id === method.id.toString()
                                                            ? 'border-[#D93F21] bg-red-50 text-[#D93F21]'
                                                            : 'border-gray-200 hover:border-gray-300 bg-gray-50'
                                                        }
                                                    `}
                                                >
                                                    <span className="font-medium text-sm">{method.nama_metode}</span>
                                                    <span className="text-xs text-gray-500 text-center">{method.keterangan}</span>
                                                </div>
                                            ))}
                                        </div>
                                    </div>

                                    <div className="grid grid-cols-2 gap-4">
                                        <div className="space-y-2">
                                            <Label>Status Pembayaran</Label>
                                            <div className="flex items-center px-3 border border-gray-300 bg-gray-200 h-12 rounded-md text-gray-600 text-sm">
                                                Menunggu Konfirmasi
                                            </div>
                                        </div>
                                        <div className="space-y-2">
                                            <Label>Nama Pengirim</Label>
                                            <div className="flex items-center px-3 border border-gray-300 bg-gray-200 h-12 rounded-md text-gray-600 text-sm">
                                                {formData.nama_lengkap}
                                            </div>
                                        </div>
                                    </div>

                                    <div className="grid grid-cols-2 gap-4">
                                        <div className="space-y-2">
                                            <Label>Jumlah Dibayar (DP)</Label>
                                            <Input 
                                                type="number"
                                                placeholder={`Min: 50000`}
                                                value={formData.jumlah_dp}
                                                onChange={e => setFormData({...formData, jumlah_dp: e.target.value})}
                                                className="border-gray-300 bg-gray-50 h-12 rounded-md"
                                            />
                                            <p className="text-xs text-gray-500">Total Harga: Rp {new Intl.NumberFormat('id-ID').format(totalHarga)}</p>
                                        </div>
                                        <div className="space-y-2">
                                            <Label>Bukti Pembayaran</Label>
                                            <div className="relative">
                                                <Input 
                                                    type="file" 
                                                    accept="image/*"
                                                    className="cursor-pointer border-gray-300 bg-gray-50 h-12 rounded-md pt-2.5"
                                                    onChange={e => setFormData({...formData, bukti_pembayaran: e.target.files?.[0] || null})}
                                                />
                                                <ImageIcon className="absolute right-3 top-3.5 text-gray-400 w-5 h-5 pointer-events-none" />
                                            </div>
                                        </div>
                                    </div>

                                    {/* Info Transfer */}
                                    <div className="mt-4 p-4 bg-gray-50 rounded-xl border border-gray-200">
                                        <p className="font-bold text-gray-800 mb-1">Informasi Rekening Tujuan Transfer</p>
                                        <p className="text-sm text-gray-600">BCA No Rekening: <span className="font-mono font-bold text-black">327-143-0051</span></p>
                                        <p className="text-sm text-gray-600">A/N: <span className="font-bold text-black">Muhammad Hatta Mustafa</span></p>
                                    </div>
                                </div>
                            )}

                            {/* === ACTION BUTTONS === */}
                            <div className="flex gap-4 mt-10 max-w-lg justify-end">
                                {/* Tombol Cancel / Back */}
                                {step > 1 ? (
                                    <Button 
                                        variant="outline" 
                                        onClick={prevStep}
                                        className="bg-[#D93F21] text-white hover:bg-[#b9351b] hover:text-white border-none rounded-full px-8 py-6 font-medium"
                                    >
                                        <ArrowLeft className="w-4 h-4 mr-2" /> Kembali
                                    </Button>
                                ) : (
                                    <Link href="/pelanggan/home">
                                        <Button 
                                            variant="outline" 
                                            className="bg-[#D93F21] text-white hover:bg-[#b9351b] hover:text-white border-none rounded-full px-8 py-6 font-medium"
                                        >
                                            Cancel
                                        </Button>
                                    </Link>
                                )}

                                {/* Tombol Next / Submit */}
                                {step < 3 ? (
                                    <Button 
                                        onClick={nextStep}
                                        className="bg-[#D93F21] hover:bg-[#b9351b] text-white rounded-full px-8 py-6 font-medium"
                                    >
                                        Next <ArrowRight className="w-4 h-4 ml-2" />
                                    </Button>
                                ) : (
                                    <Button 
                                        onClick={handleSubmit}
                                        disabled={isSubmitting}
                                        className="bg-green-600 hover:bg-green-700 text-white rounded-full px-10 py-6 font-bold shadow-lg"
                                    >
                                        {isSubmitting ? <Loader2 className="animate-spin" /> : "Selesai & Booking"}
                                    </Button>
                                )}
                            </div>

                        </div>
                    </div>

                </div>
            </div>
        </div>
    );
}